/**=====================
     01. General CSS Start
==========================**/
//Custom css Start
body {
    background-color: $body-bg-color;
    font-size: $body-font-size;
    overflow-x: hidden;
    font-family: 'Poppins', sans-serif;
    color: $theme-body-font-color;
}
ul {
    padding-left: $ul-padding-left;
    list-style-type: none;
    margin-bottom: 0;
    padding-right: $ul-padding-right;
}
* a {
    color: $primary-color;
    font-size: 16px;
}
*.btn:focus{
    box-shadow : $btn-focus-box-shadow !important;
}
p {
    font-size: $paragraph-font-size;
    line-height: $paragraph-line-height;
    letter-spacing: $paragraph-letter-spacing;
    margin: 0 0 5px;
}
code{
    color: $code-tag-color !important;
    background-color: $code-tag-bg-color;
    padding: $code-tag-padding;
    margin: $code-tag-margin;
    border-radius: $code-tag-border-radious;
    font-size: $code-font-size !important;
}
blockquote{
    border-left: $blockquote-border;
    padding: $blockquote-padding;
}
blockquote{
    &.text-center{
        border: none;
        padding: $blockquote-padding;
    }
}
blockquote{
    &.text-right{
        border-left: none;
        border-right: $blockquote-border;
        padding: $blockquote-padding;
    }
}
:focus{
    outline-color: $all-focus-outline-color;
}
.disabled {
    cursor: not-allowed;
    opacity: 0.5;
}
svg{
    vertical-align: baseline;
}
input {
    &:focus {
        outline-color: $transparent-color;
    }
}
:not(pre) {
    > code[class*=language-], pre[class*=language-] {
        background: $light-color;
    }
}
.code-box-copy__btn {
    background-color: rgba($primary-color, 0.1);
    border: 1px solid rgba($primary-color, 0.30);
}
.dark-html {
    pre, code {
        background: $dark-card-background !important;
        color: $white !important;
        text-shadow: none;
    }
    .code-box-copy__btn {
        background-color: rgba($white, 0.8) !important;
        border: 1px solid rgba($white, 0.8);
    }
}
ul {
    li {
        padding-bottom: 2px;
    }
}
//Custom css End

pre{
    background-color: $pre-tag-bg-color;
    padding: $pre-tag-padding;
}

a{
    &:hover{
        text-decoration: none;
        color: $primary-color;
    }
}

.nav-tabs {
    .nav-item {
        margin-bottom: -3px;
    }
}
.pre-cls {
    background-color: $dark-card-background;
    padding: 20px;
}
.p-25 {
    padding: 25px;
}
.img-border {
    padding: 5px;
    border: 2px dashed black;
    margin: 10px;
    width: 800px;
}
.appDesc {
    display: flex;
    align-items: center !important;
}
.data-block {
    background-color: rgba(224, 217, 209, 0.3);
    padding: 20px;
    border-left: 5px solid rgb(47, 60, 78);
}

